// 
export const getName = (list: { name: string }[]) => {
  let str = "";
  list.map((item: { name: string }, index: number) => {
    str += index === 0 ? item.name : "/" + item.name;
    return item;
  });
  return str;
};

//
export const isEmptyObject = (obj: any) => !obj || Object.keys(obj).length === 0;

//  css3 
const elementStyle = document.createElement("div").style;

const vendor = (() => {
  //  transition 
  const transformNames = {
    webkit: "webkitTransform",
    Moz: "MozTransform",
    O: "OTransfrom",
    ms: "msTransform",
    standard: "Transform"
  };
  for (let key in transformNames) {
    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
    // @ts-ignore
    if (elementStyle[transformNames[key]] !== undefined) {
      return key;
    }
  }
  return false;
})();

export function prefixStyle(style: any) {
  if (vendor === false) {
    return false;
  }
  if (vendor === "standard") {
    return style;
  }
  return vendor + style.charAt(0).toUpperCase() + style.substr(1);
}

//
export const formatPlayTime = (interval: number) => {
  interval = interval | 0; // |0
  const minute = (interval / 60) | 0;
  const second = (interval % 60).toString().padStart(2, "0");
  return `${minute}:${second}`;
};

function getRandomInt(min: number, max: number) {
  return Math.floor(Math.random() * (max - min + 1) + min);
}

// 
export function shuffle(arr: any[]) {
  const new_arr: any[] = [];
  arr.forEach(item => {
    new_arr.push(item);
  });
  for (let i = 0; i < new_arr.length; i++) {
    let j = getRandomInt(0, i);
    let t = new_arr[i];
    new_arr[i] = new_arr[j];
    new_arr[j] = t;
  }
  return new_arr;
}

// 
export const findIndex = (song: { id: any }, list: any[]) => {
  return list.findIndex(item => {
    return song.id === item.id;
  });
};
//url
export const getSongUrl = (id: any) => {
  return `https://music.163.com/song/media/outer/url?id=${id}.mp3`;
};

// 
export const filterIndex = (rankList: string | any[]) => {
  if (rankList.length) {
    for (let i = 0; i < rankList.length - 1; i++) {
      if (rankList[i].tracks.length && !rankList[i + 1].tracks.length) {
        return i + 1;
      }
    }
  }
};
export const getCount = (count: number) => {
  if (count < 0) return;
  if (count < 10000) {
    return count;
  } else if (Math.floor(count / 10000) < 10000) {
    return Math.floor(count / 1000) / 10 + "";
  } else {
    return Math.floor(count / 10000000) / 10 + "";
  }
};
//
export const trimPhone = (val: string) => val.replace(/(^\s+)|(\s+$)|\s+/g, "");
